div.product_details {
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	div.box_art {
		width: auto;
	}

	div.info_and_options {
		width: 65%;

		h2 {
			font-size: 3em;
		}

		p {
			margin-top: 30px;
			font-size: 1.4em;
		}

		button.buy {
			margin-top: 40px;
			border: 2px solid black;
			border-radius: 3px;
			padding: 12px 5%;
			background-color: transparent;
			font-weight: bold;
			cursor: pointer;
		}

		button.buy:hover {
			background-color: #ddd;
		}
	}
}

div.product_screenshots {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	margin: 5vh auto;
	border: 2px solid black;
	border-radius: 10px;
	padding: 2% 1%;

	div.left_button, div.right_button {
		border-radius: 10px;
		padding: 4vw 0.5%;
		background-color: #eee;
		font-size: 3em;
		font-weight: bold;
		cursor: pointer;
	}

	div.left_button:hover, div.right_button:hover {
		background-color: #ccc;
		box-shadow: inset 0 0 5px #aaa;
	}

	div.image_container {
		margin: 0 1%;

		img {
			width: auto;
			height: auto;
			max-width: 100%;
			max-height: 300px;
			cursor: pointer;
			transition: transform 0.2s;
		}
	}

	div.image_container:hover {
		img {
			transform: scale(1.1);
			box-shadow: 0 0 10px #aaa;
		}
	}
}

div#image_fullscreen_overlay_container {
	position: fixed;
	display: flex;
	justify-content: space-between;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 2;
	background: rgba(0, 0, 0, 0.98);
	animation: scale_container 0.2s;

	img.image_fullscreen {
		display: block;
		width: 60vw;
		height: 80vh;
		object-fit: contain;
		padding: 30px;
		border: 2px solid gray;
		border-radius: 5px;
		margin: auto;
		margin-top: 3.5%;
		background-color: black;
		animation: scale_image 0.2s;
	}

	#prev_image, #next_image, #close_button {
		cursor: pointer;
	}

	#prev_image, #next_image {
		height: 25%;
		margin: auto 0;
		border: none;
		padding: 3.5%;
		font-size: 3em;
		background-color: transparent;
		color: #fff;
	}

	#close_button {
		position: absolute;
		top: 1%;
		right: 1%;
		border: none;
		padding: 4px 8px;
		background-color: transparent;

		img {
			width: 36px;
			height: 36px;
			filter: invert(100%) sepia(63%) saturate(0%) hue-rotate(65deg) brightness(105%) contrast(101%);
		}
	}

	#prev_image:hover, #next_image:hover, #close_button:hover {
		background-color: #333;
	}
	
}

@keyframes scale_container {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes scale_image {
	0% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}